import React, { Component } from 'react';

class WelcomeView extends Component {
  render() {
    return (
      <main role="main">
        <div className="container">
          <h1 className="text-center">Welcome!</h1>
        </div>
      </main>
    );
  }
}

export default WelcomeView;



